import React, {useState} from 'react';
import {api} from "../../../base/axios";
import {Link,useNavigate} from "react-router-dom";

const Registration = () => {
    const history = useNavigate();
    const [mail, setMail] = useState("")
    const [password, setPassword] = useState("")
    const [password2, setPassword2] = useState("")
    const [error, setError] = useState(null)

    const registerUser = () => {

        if (password !== password2) {
            setError("  ")
            return
        }

        if (!mail.match( /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/)) {
            setError("eMail ")
        } else if (password.length < 6) {
            setError("    6 ")
        } else {
            api.post("auth/register", {email: mail, password}).then((res) => {
                setError(null)
                history('/');
            }).catch(() => {
                setError("   ")
            })
        }
    }

    return (
        <div className="auth">
            <div className="auth-wrap d-flex align-items-center justify-content-center flex-column">
                <div className="logo" >
                    WEDDING APP
                </div>
                <h4 className="auth-title"></h4>
                <label className="mt-4">
                    <input className="auth-input" placeholder="" type="email" name="mail" value={mail}
                           onChange={e => setMail(e.target.value)}/>
                </label>
                <label className="mt-3">
                    <input className="auth-input" placeholder="" type="text" name="password" value={password}
                           onChange={e => setPassword(e.target.value)}/>
                </label>
                <label className="mt-3">
                    <input className="auth-input" placeholder=" " type="text" name="password"
                           value={password2} onChange={e => setPassword2(e.target.value)}/>
                </label>
                {error && (
                    <div className="error-title">{error}</div>
                )}
                <Link className="mt-2 auth-link" to={"/"}> </Link>
                <div className="mt-2 auth-btn" onClick={registerUser}></div>
            </div>
        </div>
    );
};

export default Registration;
